mce-search {
  display: inline-block;
  position: relative;
}
mce-search > label mce-icon {
  vertical-align: initial;
  cursor: pointer;
}
mce-app-bar .mce-icons mce-search mce-icon {
  margin: inherit;
}

mce-app-bar .mce-icons mce-search {
  margin: 16px;
  position: absolute;
  right: 0; 
  z-index: 1; 
  white-space:nowrap; 
  background-color: var(--theme-color-600, #757575); 
  box-shadow: -20px 0px 15px 0px var(--theme-color-600, #757575);
}

mce-search .mce-input-wrapper {
  position: relative;
  display: inline-block;
  border-bottom: 1px solid var(--theme-color-300, #bdbdbd);
  max-width: 0;
  overflow: hidden;
  height: 24px;
  visibility: hidden;
}
mce-search.mce-visible .mce-input-wrapper,
mce-search .mce-input-wrapper.mce-visible {
  transition: all 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  max-width: 999px;
  overflow: auto;
  visibility: visible;
}
mce-search .mce-input-wrapper input {
  border: none;
  font-size: inherit;
  padding: 0;
  height: 24px;
  vertical-align: bottom;
  outline: none;
  background-color: inherit;
}
mce-search .mce-input-wrapper .mce-clear {
  vertical-align: bottom;
  padding: 0;
  color: var(--theme-color-500, #9e9e9e);
}

mce-search.mce-dark-bg .mce-input-wrapper input,
mce-search.mce-dark-bg .mce-input-wrapper .mce-clear {
  background-color: transparent;
  color: var(--theme-color-50, #fafafa);
}
mce-search.mce-dark-bg .mce-input-wrapper ::placeholder {
  color: var(--theme-color-400, #bdbdbd) !important;
} 